.p
{
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(234, 242,242);
  border-radius: 10px 10px 0px 0px ;
  overflow: hidden;

}
.p-browser
{
height: 20px;
background-color: rgb(34, 240, 120);
display: flex;
align-items: center;
}
.p-circal
{
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}
.p-img
{
  width: 100%;
transition: all 10s ease-out;
}
.p:hover .p-img
{
  transform: translateZ(-100%);
}

@media  screen and (max-width:480px) {
  
  .p{
    width: 40%;
    height: 20vh;
  }
}