.i{
  display: flex;
  height: 100vh;
}
.i-left{
  display:flex;
  align-items: center;
  justify-content: center;
}
.i-right{
  flex: 1;
  position: relative;
}
.i-left-wrapper
{
  padding: 50px;
  height: 50%;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro
{
  font-size: 30px;
  font-weight: 300;
}
.i-name
{
  font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-item
{
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #3b3ee4;
  /* background-color: aquamarine; */
  display: flex;
  align-items: center;
}
.i-title-wrapper
{
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}
@keyframes move 
{
  25%{
    transform: translatey(-50px);
  }
  50%{transform: translatey(-100px);}
  75%{transform: translatey(-150px);}
  100%{transform: translatey(-200px);}
}
.i-img
{
 margin:  auto 360px;
  width: 60%;
  height: 100%;
  object-fit: cover;
  border: none;
  border-bottom-left-radius: 50px;
  position: absolute;
}
.i-bg
{
  clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 13% 100%, 0 51%, 15% 0);
 
  height: 90%;
  width: 90%;
  background-color: rgb(255, 174, 0);
  position: absolute;
  top: 0;
  right: 0;
}

.i-scroll
{
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}



@media screen and (max-width:480px)
{
  .i{
    flex-direction: column;
  }
}

.i-left-wrapper
{
  padding:10px;
  align-items: center;
  justify-content:center;
  text-align: center;
  height: 100%;
}
.i-desc
{
  display: none;
}